.board{
  display: flex;
  flex-direction: column;
  gap: 1px;
}

.board-row{
  display: flex;
  flex-direction: row;
  gap: 1px;
}

.square{
  border-radius: 5px;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid black;
  font-size: 56px;
  padding: 30px;
  background-color: #ced4da;
}

.square:hover{
  background-color: #b9bec3;
  transition: ease-in-out;
  scale: 1.1;
  border-radius: 15px;
  transition-duration: 0.2s;
}

.square:active{
  scale: 1.1;
}

.x-symbol{
  color: #c1121f;
}

.o-symbol{
  color: #0a415e;
}

h1{
  font-family: 'Courier New', Courier, monospace;
  font-size: 48px;
}

.status{
  margin-top: 5%;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 800;
  font-size: 28px;
}

.reset{
  margin-top: 2%;
  padding: 16px;
  font-size: 16px;
  background-color: #ffffff;
  color: black;
  border: 2px solid black;
  border-radius: 15px;
  padding: 16px;
  transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.reset:hover{
  scale: 1.2;
  color: white;
  background-color: black;
  border: 2px solid white;
}